<template>
    <div class="jltac-tab-pane" id="post-types">
        <div class="jltac-columns">
            <div class="jltac-column jltac-is-4">
                <div class="jltac-tabs">
                    <ul class="jltac-nav-tabs">
                        <router-link
                            v-for="post_type_data in post_types"
                            :key="post_type_data.name"
                            :to="'/post-types/' + post_type_data.name"
                            v-slot="{ navigate, isActive }"
                            custom
                            exact>
                            <li
                                class="jltac-nav-item"
                                @click="navigate"
                                @keypress.enter="navigate"
                                role="link"
                                :class="[isActive && 'router-link-active']">
                                <a
                                    class="jltac-nav-link"
                                    :class="[post_type == post_type_data.name && 'active']"
                                    href="javascript:void(0);">
                                    <span>
                                        <svg
                                            width="23"
                                            height="23"
                                            viewBox="0 0 23 23"
                                            fill="none"
                                            xmlns="http://www.w3.org/2000/svg">
                                            <path
                                                d="M10 3.99998H5.8C4.11984 3.99998 3.27976 3.99998 2.63803 4.32696C2.07354 4.61458 1.6146 5.07353 1.32698 5.63801C1 6.27975 1 7.11983 1 8.79998V17.2C1 18.8801 1 19.7202 1.32698 20.362C1.6146 20.9264 2.07354 21.3854 2.63803 21.673C3.27976 22 4.11984 22 5.8 22H14.2C15.8802 22 16.7202 22 17.362 21.673C17.9265 21.3854 18.3854 20.9264 18.673 20.362C19 19.7202 19 18.8801 19 17.2V13M6.99997 16H8.67452C9.1637 16 9.40829 16 9.63846 15.9447C9.84254 15.8957 10.0376 15.8149 10.2166 15.7053C10.4184 15.5816 10.5914 15.4086 10.9373 15.0627L20.5 5.49998C21.3284 4.67156 21.3284 3.32841 20.5 2.49998C19.6716 1.67156 18.3284 1.67155 17.5 2.49998L7.93723 12.0627C7.59133 12.4086 7.41838 12.5816 7.29469 12.7834C7.18504 12.9624 7.10423 13.1574 7.05523 13.3615C6.99997 13.5917 6.99997 13.8363 6.99997 14.3255V16Z"
                                                stroke="black"
                                                stroke-width="2"
                                                stroke-linecap="round"
                                                stroke-linejoin="round" />
                                        </svg>
                                    </span>
                                    <span class="jltac-post-types">{{ post_type_data.title }}</span>
                                    <span
                                        v-if="!isPro() && post_type_data.is_pro"
                                        class="adminify-column-post--pro"
                                        style="margin-left: 4px"
                                        >- (Pro)</span
                                    >
                                </a>
                            </li>
                        </router-link>
                    </ul>
                </div>
            </div>

            <div class="jltac-column">
                <div class="jltac-tab-content">
                    <router-view :key="$route.fullPath" v-if="post_types.length"></router-view>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data: () => ({
        loading: false,
        post_types: [],
        post_type: null,
    }),

    async mounted() {
        this.post_type = this.$route.params.post_type;

        await this.load_post_type_data();

        if (!this.post_type && this.post_types.length) {
            this.$router.push(`/post-types/${this.post_types[0].name}`);
        }
    },

    methods: {
        async load_post_type_data() {
            this.post_types = await this.$store.dispatch("get_post_types");
        },

        set_parent_data() {
            this.$parent.post_types = this.post_types;
        },
    },

    watch: {
        post_types: {
            deep: true,
            handler() {
                this.set_parent_data();
            },
        },
    },
};
</script>
