___CSS_LOADER_EXPORT___.push([module.id, "\n.ghost {\n    opacity: 0.5;\n    background: #a7a7aa;\n}\n", "",{"version":3,"sources":["webpack://./dev/admin-columns/pages/post-type.vue"],"names":[],"mappings":";AAihBA;IACA,YAAA;IACA,mBAAA;AACA","sourcesContent":["<template>\n    <div class=\"jltac-tab-pane\" :id=\"post_type.name\">\n        <draggable\n            v-model=\"post_type.display_columns\"\n            v-bind=\"dragOptions\"\n            @start=\"drag = true\"\n            @end=\"drag = false\">\n            <div\n                ref=\"accordion\"\n                class=\"jltac-accordion\"\n                :class=\"column.forceOpen && 'jltac-accordion-force--open jltac-accordion--open'\"\n                v-for=\"(column, colIndex) in post_type.display_columns\"\n                :key=\"column.name + colIndex\">\n                <div class=\"jltac-accordion-title\">\n                    <svg\n                        class=\"drag-icon is-pulled-left is-clickable mr-2\"\n                        width=\"24\"\n                        height=\"24\"\n                        viewBox=\"0 0 24 24\"\n                        fill=\"none\"\n                        xmlns=\"http://www.w3.org/2000/svg\">\n                        <path\n                            d=\"M12 14C13.1046 14 14 13.1046 14 12C14 10.8954 13.1046 10 12 10C10.8954 10 10 10.8954 10 12C10 13.1046 10.8954 14 12 14Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M12 7C13.1046 7 14 6.10457 14 5C14 3.89543 13.1046 3 12 3C10.8954 3 10 3.89543 10 5C10 6.10457 10.8954 7 12 7Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M12 21C13.1046 21 14 20.1046 14 19C14 17.8954 13.1046 17 12 17C10.8954 17 10 17.8954 10 19C10 20.1046 10.8954 21 12 21Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M5 14C6.10457 14 7 13.1046 7 12C7 10.8954 6.10457 10 5 10C3.89543 10 3 10.8954 3 12C3 13.1046 3.89543 14 5 14Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M5 7C6.10457 7 7 6.10457 7 5C7 3.89543 6.10457 3 5 3C3.89543 3 3 3.89543 3 5C3 6.10457 3.89543 7 5 7Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M5 21C6.10457 21 7 20.1046 7 19C7 17.8954 6.10457 17 5 17C3.89543 17 3 17.8954 3 19C3 20.1046 3.89543 21 5 21Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M19 14C20.1046 14 21 13.1046 21 12C21 10.8954 20.1046 10 19 10C17.8954 10 17 10.8954 17 12C17 13.1046 17.8954 14 19 14Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M19 7C20.1046 7 21 6.10457 21 5C21 3.89543 20.1046 3 19 3C17.8954 3 17 3.89543 17 5C17 6.10457 17.8954 7 19 7Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                        <path\n                            d=\"M19 21C20.1046 21 21 20.1046 21 19C21 17.8954 20.1046 17 19 17C17.8954 17 17 17.8954 17 19C17 20.1046 17.8954 21 19 21Z\"\n                            fill=\"#4E4B66\"\n                            fill-opacity=\"0.72\" />\n                    </svg>\n\n                    <div class=\"jltac-accordion-type-title\">\n                        <span class=\"admin-column-name\" v-text=\"removeTags(column.label)\"></span>\n                        <!-- <div class=\"jltac-accordion-actions\">\n                            <a\n                                href=\"#\"\n                                class=\"edit-button\"\n                                @click.prevent=\"toggleAccordion($event, column)\"\n                                >Edit</a\n                            >\n                            <a href=\"#\" class=\"remove-button\" @click.prevent=\"removeColumn\"\n                                >Remove</a\n                            >\n                        </div> -->\n                        <span class=\"jltac-accordion-type\">{{ post_type.title }}</span>\n                    </div>\n                    <div class=\"jltac-accordion-helper\">\n                        <button\n                            class=\"jltac-helper-button jltac-accordion-clone\"\n                            @click=\"cloneColumn(column)\">\n                            <svg\n                                width=\"14\"\n                                height=\"14\"\n                                viewBox=\"0 0 22 22\"\n                                fill=\"none\"\n                                xmlns=\"http://www.w3.org/2000/svg\">\n                                <path\n                                    d=\"M4 14C3.06812 14 2.60218 14 2.23463 13.8478C1.74458 13.6448 1.35523 13.2554 1.15224 12.7654C1 12.3978 1 11.9319 1 11V4.2C1 3.0799 1 2.51984 1.21799 2.09202C1.40973 1.71569 1.71569 1.40973 2.09202 1.21799C2.51984 1 3.0799 1 4.2 1H11C11.9319 1 12.3978 1 12.7654 1.15224C13.2554 1.35523 13.6448 1.74458 13.8478 2.23463C14 2.60218 14 3.06812 14 4M11.2 21H17.8C18.9201 21 19.4802 21 19.908 20.782C20.2843 20.5903 20.5903 20.2843 20.782 19.908C21 19.4802 21 18.9201 21 17.8V11.2C21 10.0799 21 9.51984 20.782 9.09202C20.5903 8.71569 20.2843 8.40973 19.908 8.21799C19.4802 8 18.9201 8 17.8 8H11.2C10.0799 8 9.51984 8 9.09202 8.21799C8.71569 8.40973 8.40973 8.71569 8.21799 9.09202C8 9.51984 8 10.0799 8 11.2V17.8C8 18.9201 8 19.4802 8.21799 19.908C8.40973 20.2843 8.71569 20.5903 9.09202 20.782C9.51984 21 10.0799 21 11.2 21Z\"\n                                    stroke=\"#0cce6b\"\n                                    stroke-width=\"2\"\n                                    stroke-linecap=\"round\"\n                                    stroke-linejoin=\"round\" />\n                            </svg>\n                        </button>\n                        <button\n                            class=\"jltac-helper-button jltac-accordion-remove\"\n                            @click=\"removeColumn\">\n                            <svg\n                                width=\"10\"\n                                height=\"10\"\n                                viewBox=\"0 0 14 14\"\n                                fill=\"none\"\n                                xmlns=\"http://www.w3.org/2000/svg\">\n                                <path\n                                    d=\"M13 1L1 13M1 1L13 13\"\n                                    stroke=\"#a3b18a\"\n                                    stroke-width=\"2\"\n                                    stroke-linecap=\"round\"\n                                    stroke-linejoin=\"round\" />\n                            </svg>\n                        </button>\n                        <button class=\"jltac-helper-button jltac-accordion-toggler\">\n                            <svg\n                                width=\"14\"\n                                height=\"8\"\n                                viewBox=\"0 0 14 8\"\n                                fill=\"none\"\n                                xmlns=\"http://www.w3.org/2000/svg\">\n                                <path\n                                    d=\"M1 1L7 7L13 1\"\n                                    stroke=\"#a3b18a\"\n                                    stroke-width=\"2\"\n                                    stroke-linecap=\"round\"\n                                    stroke-linejoin=\"round\" />\n                            </svg>\n                        </button>\n                    </div>\n                </div>\n\n                <!-- <div class=\"accordion-opener\" @click=\"toggleAccordion($event, column)\"></div> -->\n                <div class=\"accordion-opener\"></div>\n\n                <div class=\"jltac-accordion-body\">\n                    <template v-for=\"(field, key) in post_type.fields\">\n                        <div\n                            class=\"jltac-inner-content jltac-content-type\"\n                            :key=\"key\"\n                            v-if=\"field.id == 'type'\">\n                            <div class=\"jltac-columns\">\n                                <div class=\"jltac-column jltac-is-4\">\n                                    <label\n                                        :for=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        >{{ field.title }}</label\n                                    >\n                                </div>\n                                <div class=\"jltac-column\">\n                                    <select\n                                        :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        class=\"select-type\"\n                                        :placeholder=\"field.placeholder\"\n                                        v-model=\"column.name\"\n                                        @focus=\"editing(column)\"\n                                        @change=\"changedType(column)\">\n                                        <template\n                                            v-for=\"(\n                                                parent_option, parent_option_key\n                                            ) in field.options\">\n                                            <optgroup\n                                                v-if=\"parent_option.group\"\n                                                :label=\"parent_option.group\"\n                                                :key=\"parent_option_key\">\n                                                <option\n                                                    v-for=\"(\n                                                        option, option_val\n                                                    ) in parent_option.options\"\n                                                    :key=\"option_val\"\n                                                    :value=\"option_val\"\n                                                    :selected=\"option_val == column.name\">\n                                                    {{ option }}\n                                                </option>\n                                            </optgroup>\n\n                                            <option\n                                                v-else\n                                                :key=\"parent_option.name\"\n                                                :value=\"parent_option.name\"\n                                                :selected=\"parent_option.name == column.name\">\n                                                {{ parent_option.title }}\n                                            </option>\n                                        </template>\n                                    </select>\n                                </div>\n                            </div>\n                        </div>\n\n                        <div\n                            class=\"jltac-inner-content jltac-content-type\"\n                            :key=\"key\"\n                            v-if=\"field.id == 'function_name' && column.name == 'function'\">\n                            <div class=\"jltac-columns\">\n                                <div class=\"jltac-column jltac-is-4\">\n                                    <label\n                                        :for=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        >{{ field.title }}</label\n                                    >\n                                </div>\n                                <div class=\"jltac-column\">\n                                    <input\n                                        :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        type=\"text\"\n                                        :placeholder=\"field.placeholder\"\n                                        v-model=\"column.function_name\"\n                                        @change=\"changedType(column)\" />\n                                </div>\n                            </div>\n                        </div>\n\n                        <div\n                            class=\"jltac-inner-content jltac-content-type\"\n                            :key=\"key\"\n                            v-if=\"field.id == 'shortcode_name' && column.name == 'shortcode'\">\n                            <div class=\"jltac-columns\">\n                                <div class=\"jltac-column jltac-is-4\">\n                                    <label\n                                        :for=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        >{{ field.title }}</label\n                                    >\n                                </div>\n                                <div class=\"jltac-column\">\n                                    <input\n                                        :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        type=\"text\"\n                                        :placeholder=\"field.placeholder\"\n                                        v-model=\"column.shortcode_name\"\n                                        @change=\"changedType(column)\" />\n                                </div>\n                            </div>\n                        </div>\n\n                        <div\n                            class=\"jltac-inner-content jltac-content-type\"\n                            :key=\"key\"\n                            v-if=\"field.id == 'label'\">\n                            <div class=\"jltac-columns\">\n                                <div class=\"jltac-column jltac-is-4\">\n                                    <label\n                                        :for=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        >{{ field.title }}</label\n                                    >\n                                </div>\n                                <div class=\"jltac-column\">\n                                    <input\n                                        :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        type=\"text\"\n                                        :placeholder=\"field.placeholder\"\n                                        v-model=\"column.label\" />\n                                </div>\n                            </div>\n                        </div>\n\n                        <div\n                            class=\"jltac-inner-content jltac-content-type\"\n                            :key=\"key\"\n                            v-if=\"field.id == 'width'\">\n                            <div class=\"jltac-columns\">\n                                <div class=\"jltac-column jltac-is-4\">\n                                    <label\n                                        :for=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        >{{ field.title }}</label\n                                    >\n                                </div>\n                                <div class=\"jltac-column\">\n                                    <div class=\"jltac-range-slider-wrapper\">\n                                        <input\n                                            type=\"text\"\n                                            class=\"jltac-range-value\"\n                                            :value=\"formatedWidthValue(column.width.value)\"\n                                            readonly />\n                                        <range-slider\n                                            class=\"slider\"\n                                            v-model=\"column.width.value\"\n                                            :min=\"0\"\n                                            :max=\"getWidthMax(column.width.unit)\"></range-slider>\n                                    </div>\n\n                                    <div class=\"jltac-unit-labels\">\n                                        <label class=\"jltac-range-width-unit\">\n                                            <input\n                                                :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                                @change=\"onSliderUnitChange(column)\"\n                                                class=\"range-width-unit-%\"\n                                                type=\"radio\"\n                                                value=\"%\"\n                                                v-model=\"column.width.unit\"\n                                                :checked=\"column.width.unit == '%'\" />%\n                                        </label>\n                                        <label class=\"jltac-range-width-unit\">\n                                            <input\n                                                :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                                @change=\"onSliderUnitChange(column)\"\n                                                class=\"range-width-unit-px\"\n                                                type=\"radio\"\n                                                value=\"px\"\n                                                v-model=\"column.width.unit\"\n                                                :checked=\"column.width.unit == 'px'\" />px\n                                        </label>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </template>\n                </div>\n            </div>\n        </draggable>\n\n        <div class=\"jltac-columns jltac-add-button-wrapper\">\n            <button class=\"jltac-add-button jltac-button\" @click=\"addNewColumn\">Add new</button>\n        </div>\n\n        <div class=\"admin-column--pro-guard\" v-if=\"!isTypePro()\" v-html=\"proNotice()\"></div>\n    </div>\n</template>\n\n<script>\nimport RangeSlider from \"vue-range-slider\";\nimport draggable from \"vuedraggable\";\nimport \"vue-range-slider/dist/vue-range-slider.css\";\n\nexport default {\n    data: () => ({\n        post_type: {},\n        current: null,\n        current_column: {},\n        drag: false,\n    }),\n\n    components: {\n        RangeSlider,\n        draggable,\n    },\n\n    mounted() {\n        this.current = this.$route.params.post_type;\n        this.post_type = this.$store.getters.post_type_by_name(this.current);\n    },\n\n    computed: {\n        dragOptions() {\n            return {\n                animation: 150,\n                group: \"description\",\n                disabled: !this.isTypePro(),\n                ghostClass: \"ghost\",\n            };\n        },\n\n        fieldTypeKeys() {\n            let typedata = this.post_type.fields.find((field) => field.id == \"type\").options;\n            let options = this.groupToOptions(typedata);\n            return Object.keys(options);\n        },\n\n        displayColumnKeys() {\n            return this.post_type.display_columns.map((column) => column.name);\n        },\n\n        // regularColumnsByGroups( groups = [] ) {\n        //     return this.post_type.fields.filter()\n        // }\n    },\n\n    methods: {\n        groupToOptions(groups) {\n            let data = {};\n            groups.forEach((group) => {\n                if (group.group) {\n                    data = jQuery.extend({}, data, group.options);\n                } else {\n                    data[group.name] = group.title;\n                }\n            });\n            return data;\n        },\n\n        isTypePro() {\n            if (this.isPro()) return true;\n            if (!this.post_type.is_pro) return true;\n            return false;\n        },\n\n        editing(column) {\n            if (!this.isTypePro()) return;\n            this.current_column = this.nonReactive(column);\n        },\n\n        onSliderUnitChange(column) {\n            if (!this.isTypePro()) return;\n            let max = this.getWidthMax(column.width.unit);\n            if (column.width.value > max) column.width.value = max;\n        },\n\n        formatedWidthValue(width) {\n            if (width == 0) return \"auto\";\n            return width;\n        },\n\n        getWidthMax(unit) {\n            if (unit == \"%\") return 100;\n            return 1000;\n        },\n\n        // toggleAccordion(event, column) {\n        //     if (!this.isTypePro()) return;\n\n        //     let $accordion = jQuery(event.target)\n        //         .closest(\".jltac-accordion\")\n        //         .toggleClass(\"jltac-accordion--open\");\n        //     let $body = $accordion.find(\".jltac-accordion-body\");\n\n        //     if ($accordion.hasClass(\"jltac-accordion--open\")) {\n        //         $body.slideDown(200, () => {\n        //             column.forceOpen = true;\n        //         });\n        //     } else {\n        //         $body.slideUp(200, () => {\n        //             $accordion.removeClass(\"jltac-accordion-force--open\");\n        //             delete column.forceOpen;\n        //         });\n        //     }\n        // },\n\n        removeColumn(event) {\n            if (!this.isTypePro()) return;\n\n            let index = jQuery(event.target).closest(\".jltac-accordion\").index();\n\n            if (index > -1) this.post_type.display_columns.splice(index, 1);\n        },\n\n        hasAddedAllFields() {\n            return this.fieldTypeKeys.every((field_key) =>\n                this.displayColumnKeys.includes(field_key)\n            );\n        },\n\n        columnsExtraCheck() {\n            return {\n                function: \"function_name\",\n                shortcode: \"shortcode_name\",\n            };\n        },\n\n        changedType(column) {\n            if (!this.isTypePro()) return;\n\n            let columnsExtraCheck = this.columnsExtraCheck();\n\n            let columns = this.post_type.display_columns.filter((_column) => {\n                if (_column.name in columnsExtraCheck) {\n                    let _field = columnsExtraCheck[_column.name];\n                    return (\n                        column.name == _column.name &&\n                        _field in column &&\n                        _field in _column &&\n                        column[_field] == _column[_field]\n                    );\n                }\n                return column.name == _column.name;\n            });\n\n            if (columns.length > 1) {\n                if (column.name in columnsExtraCheck) {\n                    let _field = columnsExtraCheck[column.name];\n                    column[_field] = \"\";\n                } else {\n                    column.name = this.current_column.name;\n                }\n\n                this.showError({\n                    title: `Can't change the column!`,\n                    content: `This field is already in use, you can't use it twice`,\n                });\n            }\n        },\n\n        getNonAddedColumnKeys() {\n            return this.fieldTypeKeys.filter(\n                (field_key) => !this.displayColumnKeys.includes(field_key)\n            );\n        },\n\n        getNewDefaults(columnKey) {\n            if (!this.isTypePro()) return;\n\n            let defaults = {\n                forceOpen: true,\n                label: this.post_type.columns[columnKey],\n                name: columnKey,\n                width: {\n                    unit: \"%\",\n                    value: \"auto\",\n                },\n            };\n\n            return defaults;\n        },\n\n        showError(data) {\n            Window.Events.$emit(\"AdminColumnsError\", data);\n        },\n\n        getAddableColumnKeys() {\n            let extraKeys = Object.keys(this.columnsExtraCheck());\n            return this.getNonAddedColumnKeys()\n                .filter((_key) => !extraKeys.includes(_key))\n                .concat(extraKeys);\n        },\n\n        addNewColumn() {\n            if (!this.isTypePro()) return;\n            this.post_type.display_columns.push(\n                this.getNewDefaults(this.getAddableColumnKeys()[0])\n            );\n        },\n\n        cloneColumn(column) {\n            if (!this.isTypePro()) return;\n\n            let columnKey = column.name;\n            if (!(columnKey in this.columnsExtraCheck())) {\n                columnKey = this.getAddableColumnKeys()[0];\n            }\n            let newColumn = this.getNewDefaults(columnKey);\n            newColumn.width = this.nonReactive(column.width);\n            this.post_type.display_columns.push(newColumn);\n        },\n    },\n};\n</script>\n\n<style>\n.ghost {\n    opacity: 0.5;\n    background: #a7a7aa;\n}\n</style>\n"],"sourceRoot":""}]);
