                              class=\"edit-button\"\n                                @click.prevent=\"toggleAccordion($event, column)\"\n                                >Edit</a\n                            >\n                            <a href=\"#\" class=\"remove-button\" @click.prevent=\"removeColumn\"\n                                >Remove</a\n                            >\n                        </div> -->\n                        <span class=\"jltac-accordion-type\">{{ taxonomy.title }}</span>\n                    </div>\n\n                    <div class=\"jltac-accordion-helper\">\n                        <button\n                            class=\"jltac-helper-button jltac-accordion-clone\"\n                            @click=\"cloneColumn(column)\">\n                            <svg\n                                width=\"14\"\n                                height=\"14\"\n                                viewBox=\"0 0 22 22\"\n                                fill=\"none\"\n                                xmlns=\"http://www.w3.org/2000/svg\">\n                                <path\n                                    d=\"M4 14C3.06812 14 2.60218 14 2.23463 13.8478C1.74458 13.6448 1.35523 13.2554 1.15224 12.7654C1 12.3978 1 11.9319 1 11V4.2C1 3.0799 1 2.51984 1.21799 2.09202C1.40973 1.71569 1.71569 1.40973 2.09202 1.21799C2.51984 1 3.0799 1 4.2 1H11C11.9319 1 12.3978 1 12.7654 1.15224C13.2554 1.35523 13.6448 1.74458 13.8478 2.23463C14 2.60218 14 3.06812 14 4M11.2 21H17.8C18.9201 21 19.4802 21 19.908 20.782C20.2843 20.5903 20.5903 20.2843 20.782 19.908C21 19.4802 21 18.9201 21 17.8V11.2C21 10.0799 21 9.51984 20.782 9.09202C20.5903 8.71569 20.2843 8.40973 19.908 8.21799C19.4802 8 18.9201 8 17.8 8H11.2C10.0799 8 9.51984 8 9.09202 8.21799C8.71569 8.40973 8.40973 8.71569 8.21799 9.09202C8 9.51984 8 10.0799 8 11.2V17.8C8 18.9201 8 19.4802 8.21799 19.908C8.40973 20.2843 8.71569 20.5903 9.09202 20.782C9.51984 21 10.0799 21 11.2 21Z\"\n                                    stroke=\"#0cce6b\"\n                                    stroke-width=\"2\"\n                                    stroke-linecap=\"round\"\n                                    stroke-linejoin=\"round\" />\n                            </svg>\n                        </button>\n                        <button\n                            class=\"jltac-helper-button jltac-accordion-remove\"\n                            @click=\"removeColumn\">\n                            <svg\n                                width=\"10\"\n                                height=\"10\"\n                                viewBox=\"0 0 14 14\"\n                                fill=\"none\"\n                                xmlns=\"http://www.w3.org/2000/svg\">\n                                <path\n                                    d=\"M13 1L1 13M1 1L13 13\"\n                                    stroke=\"#a3b18a\"\n                                    stroke-width=\"2\"\n                                    stroke-linecap=\"round\"\n                                    stroke-linejoin=\"round\" />\n                            </svg>\n                        </button>\n                        <button class=\"jltac-helper-button jltac-accordion-toggler\">\n                            <svg\n                                width=\"14\"\n                                height=\"8\"\n                                viewBox=\"0 0 14 8\"\n                                fill=\"none\"\n                                xmlns=\"http://www.w3.org/2000/svg\">\n                                <path\n                                    d=\"M1 1L7 7L13 1\"\n                                    stroke=\"#a3b18a\"\n                                    stroke-width=\"2\"\n                                    stroke-linecap=\"round\"\n                                    stroke-linejoin=\"round\" />\n                            </svg>\n                        </button>\n                    </div>\n                </div>\n\n                <!-- <div class=\"accordion-opener\" @click=\"toggleAccordion($event, column)\"></div> -->\n                <div class=\"accordion-opener\"></div>\n\n                <div class=\"jltac-accordion-body\">\n                    <template v-for=\"(field, key) in taxonomy.fields\">\n                        <div\n                            class=\"jltac-inner-content jltac-content-type\"\n                            :key=\"key\"\n                            v-if=\"column.fields.includes(field.id)\">\n                            <div class=\"jltac-columns\">\n                                <div class=\"jltac-column jltac-is-4\">\n                                    <label\n                                        :for=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        >{{ field.title }}</label\n                                    >\n                                </div>\n\n                                <div class=\"jltac-column\" v-if=\"field.type == 'select'\">\n                                    <select\n                                        :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        class=\"select-type\"\n                                        :placeholder=\"field.placeholder\"\n                                        v-model=\"column.name\"\n                                        @focus=\"editing(column)\"\n                                        @change=\"changedType(column)\">\n                                        <!-- <optgroup label=\"Default\"></optgroup> -->\n                                        <option\n                                            v-for=\"(option, option_val) in field.options\"\n                                            :key=\"option_val\"\n                                            :value=\"option_val\"\n                                            :selected=\"option_val == column.name\">\n                                            {{ option }}\n                                        </option>\n                                    </select>\n                                </div>\n\n                                <div class=\"jltac-column\" v-if=\"field.type == 'text'\">\n                                    <input\n                                        :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                        type=\"text\"\n                                        :placeholder=\"field.placeholder\"\n                                        v-model=\"column.label\" />\n                                </div>\n\n                                <div class=\"jltac-column\" v-if=\"field.type == 'slider'\">\n                                    <div class=\"jltac-range-slider-wrapper\">\n                                        <input\n                                            type=\"text\"\n                                            class=\"jltac-range-value\"\n                                            :value=\"formatedWidthValue(column.width.value)\"\n                                            readonly />\n                                        <range-slider\n                                            class=\"slider\"\n                                            v-model=\"column.width.value\"\n                                            :min=\"0\"\n                                            :max=\"getWidthMax(column.width.unit)\"></range-slider>\n                                    </div>\n\n                                    <div class=\"jltac-unit-labels\">\n                                        <label class=\"jltac-range-width-unit\">\n                                            <input\n                                                :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                                @change=\"onSliderUnitChange(column)\"\n                                                class=\"range-width-unit-%\"\n                                                type=\"radio\"\n                                                value=\"%\"\n                                                v-model=\"column.width.unit\"\n                                                :checked=\"column.width.unit == '%'\" />%\n                                        </label>\n                                        <label class=\"jltac-range-width-unit\">\n                                            <input\n                                                :name=\"`content-${colIndex}-${column.name}-${field.id}`\"\n                                                @change=\"onSliderUnitChange(column)\"\n                                                class=\"range-width-unit-px\"\n                                                type=\"radio\"\n                                                value=\"px\"\n                                                v-model=\"column.width.unit\"\n                                                :checked=\"column.width.unit == 'px'\" />px\n                                        </label>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </template>\n                </div>\n            </div>\n        </draggable>\n\n        <div class=\"jltac-columns jltac-add-button-wrapper\">\n            <button class=\"jltac-add-button jltac-button\" @click=\"addNewColumn\">Add new</button>\n        </div>\n\n        <div class=\"admin-column--pro-guard\" v-if=\"!isTaxPro()\" v-html=\"proNotice()\"></div>\n    </div>\n</template>\n\n<script>\nimport RangeSlider from \"vue-range-slider\";\nimport draggable from \"vuedraggable\";\nimport \"vue-range-slider/dist/vue-range-slider.css\";\n\nexport default {\n    data: () => ({\n        taxonomy: {},\n        current: null,\n        current_column: {},\n        drag: false,\n    }),\n\n    components: {\n        RangeSlider,\n        draggable,\n    },\n\n    mounted() {\n        this.current = this.$route.params.taxonomy;\n        this.taxonomy = this.$store.getters.taxonomy_by_name(this.current);\n    },\n\n    computed: {\n        dragOptions() {\n            return {\n                animation: 150,\n                group: \"description\",\n                disabled: !this.isTaxPro(),\n                ghostClass: \"ghost\",\n            };\n        },\n\n        fieldTypeKeys() {\n            return Object.keys(this.taxonomy.fields.find((field) => field.id == \"type\").options);\n        },\n\n        displayColumnKeys() {\n            return this.taxonomy.display_columns.map((column) => column.name);\n        },\n    },\n\n    methods: {\n        isTaxPro() {\n            if (this.isPro()) return true;\n            if (!this.taxonomy.is_pro) return true;\n            return false;\n        },\n\n        editing(column) {\n            if (!this.isTaxPro()) return;\n            this.current_column = this.nonReactive(column);\n        },\n\n        changedType(column) {\n            if (!this.isTaxPro()) return;\n\n            let keys = this.displayColumnKeys.filter((field_key) => field_key == column.name);\n\n            if (keys.length > 1) {\n                let label = jQuery(this.taxonomy.columns[column.name]).text(); // fix for HTML value\n                if (!label) label = this.taxonomy.columns[column.name];\n\n                if (this.current_column.name) column.name = this.current_column.name;\n\n                this.showError({\n                    title: `Can't change the column!`,\n                    content: `${label} is already in use, you can't use it twice`,\n                });\n            }\n        },\n\n        onSliderUnitChange(column) {\n            if (!this.isTaxPro()) return;\n            let max = this.getWidthMax(column.width.unit);\n            if (column.width.value > max) column.width.value = max;\n        },\n\n        formatedWidthValue(width) {\n            if (width == 0) return \"auto\";\n            return width;\n        },\n\n        getWidthMax(unit) {\n            if (unit == \"%\") return 100;\n            return 1000;\n        },\n\n        // toggleAccordion(event, column) {\n        //     if (!this.isTaxPro()) return;\n        //     let $accordion = jQuery(event.target)\n        //         .closest(\".jltac-accordion\")\n        //         .toggleClass(\"jltac-accordion--open\");\n\n        //     let $body = $accordion.find(\".jltac-accordion-body\");\n\n        //     if ($accordion.hasClass(\"jltac-accordion--open\")) {\n        //         $body.slideDown(200, () => {\n        //             column.forceOpen = true;\n        //         });\n        //     } else {\n        //         $body.slideUp(200, () => {\n        //             $accordion.removeClass(\"jltac-accordion-force--open\");\n        //             delete column.forceOpen;\n        //         });\n        //     }\n        // },\n\n        removeColumn(event) {\n            if (!this.isTaxPro()) return;\n\n            let index = jQuery(event.target).closest(\".jltac-accordion\").index();\n\n            if (index > -1) this.taxonomy.display_columns.splice(index, 1);\n        },\n\n        hasAddedAllFields() {\n            return this.fieldTypeKeys.every((field_key) =>\n                this.displayColumnKeys.includes(field_key)\n            );\n        },\n\n        getNonAddedFieldKeys() {\n            return this.fieldTypeKeys.filter(\n                (field_key) => !this.displayColumnKeys.includes(field_key)\n            );\n        },\n\n        getNewDefaults(columnKey) {\n            if (!this.isTaxPro()) return;\n\n            return {\n                forceOpen: true,\n                fields: [\"type\", \"label\", \"width\"],\n                label: this.taxonomy.columns[columnKey],\n                name: columnKey,\n                width: {\n                    unit: \"%\",\n                    value: \"auto\",\n                },\n            };\n        },\n\n        showError(data) {\n            Window.Events.$emit(\"AdminColumnsError\", data);\n        },\n\n        addNewColumn() {\n            if (!this.isTaxPro()) return;\n\n            if (this.hasAddedAllFields())\n                return this.showError({\n                    title: `Can't add new column!`,\n                    content: `All columns are added, you can't add new column`,\n                });\n\n            let columnKey = this.getNonAddedFieldKeys()[0];\n            let newColumn = this.getNewDefaults(columnKey);\n            this.taxonomy.display_columns.push(newColumn);\n        },\n\n        cloneColumn(column) {\n            if (!this.isTaxPro()) return;\n\n            if (this.hasAddedAllFields())\n                return this.showError({\n                    title: `Can't clone column!`,\n                    content: `All columns are added, you can't add new column`,\n                });\n\n            let columnKey = this.getNonAddedFieldKeys()[0];\n            let newColumn = this.getNewDefaults(columnKey);\n            newColumn.width = this.nonReactive(column.width);\n            this.taxonomy.display_columns.push(newColumn);\n        },\n    },\n};\n</script>\n\n<style>\n.ghost {\n    opacity: 0.5;\n    background: #a7a7aa;\n}\n</style>\n"],"sourceRoot":""}]);
