<template>
    <div id="wpadminify-admin-columns">
        <div class="admin-column--message" v-if="success_message">Settings has been saved</div>

        <div class="jltac-tabs">
            <ul class="jltac-nav-tabs" role="tablist">
                <router-link :to="'/post-types'" v-slot="{ navigate, isActive }" custom>
                    <li
                        id="analyze-tab"
                        class="jltac-nav-item"
                        @click="navigate"
                        @keypress.enter="navigate"
                        role="link"
                        :class="[isActive && 'router-link-active']">
                        <a
                            class="jltac-nav-link"
                            :class="[isActive && 'active']"
                            href="javascript:void(0);">
                            <span>
                                <svg
                                    width="23"
                                    height="23"
                                    viewBox="0 0 23 23"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg">
                                    <path
                                        d="M10 3.99998H5.8C4.11984 3.99998 3.27976 3.99998 2.63803 4.32696C2.07354 4.61458 1.6146 5.07353 1.32698 5.63801C1 6.27975 1 7.11983 1 8.79998V17.2C1 18.8801 1 19.7202 1.32698 20.362C1.6146 20.9264 2.07354 21.3854 2.63803 21.673C3.27976 22 4.11984 22 5.8 22H14.2C15.8802 22 16.7202 22 17.362 21.673C17.9265 21.3854 18.3854 20.9264 18.673 20.362C19 19.7202 19 18.8801 19 17.2V13M6.99997 16H8.67452C9.1637 16 9.40829 16 9.63846 15.9447C9.84254 15.8957 10.0376 15.8149 10.2166 15.7053C10.4184 15.5816 10.5914 15.4086 10.9373 15.0627L20.5 5.49998C21.3284 4.67156 21.3284 3.32841 20.5 2.49998C19.6716 1.67156 18.3284 1.67155 17.5 2.49998L7.93723 12.0627C7.59133 12.4086 7.41838 12.5816 7.29469 12.7834C7.18504 12.9624 7.10423 13.1574 7.05523 13.3615C6.99997 13.5917 6.99997 13.8363 6.99997 14.3255V16Z"
                                        stroke="black"
                                        stroke-width="2"
                                        stroke-linecap="round"
                                        stroke-linejoin="round" />
                                </svg>
                            </span>
                            <span>Post Types</span>
                        </a>
                    </li>
                </router-link>

                <router-link :to="'/taxonomies'" v-slot="{ navigate, isActive }" custom>
                    <li
                        id="analyze-tab"
                        class="jltac-nav-item"
                        @click="navigate"
                        @keypress.enter="navigate"
                        role="link"
                        :class="[isActive && 'router-link-active']">
                        <a
                            class="jltac-nav-link"
                            :class="[isActive && 'active']"
                            href="javascript:void(0);">
                            <span>
                                <svg
                                    width="20"
                                    height="20"
                                    viewBox="0 0 20 20"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg">
                                    <path
                                        d="M1 1V11.2C1 12.8802 1 13.7202 1.32698 14.362C1.6146 14.9265 2.07354 15.3854 2.63803 15.673C3.27976 16 4.11984 16 5.8 16H13M13 16C13 17.6569 14.3431 19 16 19C17.6569 19 19 17.6569 19 16C19 14.3431 17.6569 13 16 13C14.3431 13 13 14.3431 13 16ZM1 6L13 6M13 6C13 7.65686 14.3431 9 16 9C17.6569 9 19 7.65685 19 6C19 4.34315 17.6569 3 16 3C14.3431 3 13 4.34315 13 6Z"
                                        stroke="black"
                                        stroke-width="2"
                                        stroke-linecap="round"
                                        stroke-linejoin="round" />
                                </svg>
                            </span>
                            <span>Taxonomies</span>
                        </a>
                    </li>
                </router-link>
            </ul>
        </div>

        <div class="jltac-tab-content">
            <div class="jltac-loader"></div>
            <router-view :key="$route.fullPath"></router-view>
        </div>

        <div class="jltac-save-button">
            <button class="jltac-button" :disabled="disable_save_btn" @click="save_store">
                Save Settings
            </button>
        </div>

        <!-- Popup -->
        <div class="jltac--popup-area">
            <div class="jltac--popup-container">
                <div class="jltac--popup-container_inner">
                    <div class="popup--delete-folder" v-if="popup_open">
                        <a class="jltac--popup-close" href="#" @click.prevent="hide_popup"
                            ><span class="dashicons dashicons-no-alt"></span
                        ></a>

                        <h3>{{ popup_title }}</h3>

                        <div class="jltac-popup-content" v-html="popup_details"></div>

                        <a
                            href="#"
                            class="button jltac-button jltac-popup-button"
                            @click.prevent="hide_popup"
                            >Got it</a
                        >
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data: () => ({
        post_types: [],
        taxonomies: [],
        popup_open: true,
        disable_save_btn: false,
        success_message: false,
        popup_title: "",
        popup_details: "",
    }),

    mounted() {
        Window.Events.$on("AdminColumnsError", (data) => {
            this.popup_title = data.title;
            this.popup_details = data.content;
            this.open_popup();
        });

        jQuery("body").on("click", () => {
            this.success_message = false;
        });
    },

    methods: {
        open_popup() {
            jQuery("body").addClass("jltac--popup-show");
            this.popup_open = true;
        },

        hide_popup() {
            jQuery("body").removeClass("jltac--popup-show");
            this.popup_open = false;
        },

        async save_store() {
            let post_types = this.post_types;
            let taxonomies = this.taxonomies;

            this.disable_save_btn = true;

            await this.$store.dispatch("save_store", { post_types, taxonomies });

            this.disable_save_btn = false;
            this.success_message = true;

            setTimeout(() => {
                this.success_message = false;
            }, 1500);
        },
    },
};
</script>
