<template>
    <div class="jltac-tab-pane" id="taxonomies">
        <div class="jltac-columns">
            <div class="jltac-column jltac-is-4">
                <div class="jltac-tabs">
                    <ul class="jltac-nav-tabs">
                        <router-link
                            v-for="taxonomy_data in taxonomies"
                            :key="taxonomy_data.name"
                            :to="'/taxonomies/' + taxonomy_data.name"
                            v-slot="{ navigate, isActive }"
                            custom
                            exact>
                            <li
                                class="jltac-nav-item"
                                @click="navigate"
                                @keypress.enter="navigate"
                                role="link"
                                :class="[isActive && 'router-link-active']">
                                <a
                                    class="jltac-nav-link"
                                    :class="[taxonomy == taxonomy_data.name && 'active']"
                                    href="javascript:void(0);">
                                    <span>
                                        <svg
                                            width="20"
                                            height="20"
                                            viewBox="0 0 20 20"
                                            fill="none"
                                            xmlns="http://www.w3.org/2000/svg">
                                            <path
                                                d="M1 1V11.2C1 12.8802 1 13.7202 1.32698 14.362C1.6146 14.9265 2.07354 15.3854 2.63803 15.673C3.27976 16 4.11984 16 5.8 16H13M13 16C13 17.6569 14.3431 19 16 19C17.6569 19 19 17.6569 19 16C19 14.3431 17.6569 13 16 13C14.3431 13 13 14.3431 13 16ZM1 6L13 6M13 6C13 7.65686 14.3431 9 16 9C17.6569 9 19 7.65685 19 6C19 4.34315 17.6569 3 16 3C14.3431 3 13 4.34315 13 6Z"
                                                stroke="black"
                                                stroke-width="2"
                                                stroke-linecap="round"
                                                stroke-linejoin="round" />
                                        </svg>
                                    </span>
                                    <span>{{ taxonomy_data.title }}</span>
                                    <span
                                        v-if="!isPro() && taxonomy_data.is_pro"
                                        class="adminify-column-post--pro"
                                        style="margin-left: 4px"
                                        >- (Pro)</span
                                    >
                                    <span
                                        class="jltac-taxonomy-post-type"
                                        v-if="taxonomy_data.object_type"
                                        >[{{ taxonomy_data.object_type }}]</span
                                    >
                                </a>
                            </li>
                        </router-link>
                    </ul>
                </div>
            </div>

            <div class="jltac-column">
                <div class="jltac-tab-content">
                    <router-view :key="$route.fullPath" v-if="taxonomies.length"></router-view>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data: () => ({
        loading: false,
        taxonomies: [],
        taxonomy: null,
    }),

    async mounted() {
        this.taxonomy = this.$route.params.taxonomy;

        await this.load_taxonomy_data();

        if (!this.taxonomy && this.taxonomies.length) {
            this.$router.push(`/taxonomies/${this.taxonomies[0].name}`);
        }
    },

    methods: {
        async load_taxonomy_data() {
            this.taxonomies = await this.$store.dispatch("get_taxonomies");
        },

        set_parent_data() {
            this.$parent.taxonomies = this.taxonomies;
        },
    },

    watch: {
        taxonomies: {
            deep: true,
            handler() {
                this.set_parent_data();
            },
        },
    },
};
</script>

<style>
.jltac-taxonomy-post-type {
    text-align: right;
    text-transform: capitalize;
    opacity: 0.5;
    font-size: 0.8em;
    letter-spacing: 0.3px;
    font-weight: bold;
    flex: auto;
}
</style>
